@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$azul2;
    text-align: center;
}

h1 {
    font-size: 3rem;
}
h2 {
    color: v.$blanco;
    font-size: 2.6rem;
}
h3 {
    font-size: 2rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}
.imagen-pagina {
    text-align: center;
    margin: 5rem 0;
    font-weight: bold;
}
.descripcion-pagina {
    text-align: center;
}